body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* App css begin */

body {
  padding: 0;
  margin: 0;
  overflow: hidden; 
}

/*.content {
  position: absolute;
  top: 55px;
  bottom: 45px;
  left: 0px;
  right: 0px;
  overflow: hidden;  
}*/
/* overflow-x: hidden;
overflow-y: auto; */

.center-div {
  margin: auto;
}

.text-center {
  text-align: center;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* App css end */

/* Layout css begin*/

.main-container {
  height: 100%;
  width: 100%;
  position: absolute;
  overflow: auto;
}

.main-content {
  margin: 15px 50px 5px 80px;
}

@media screen and (max-width: 1025px) {
  .main-content {
      margin: 5px 0px 0px 0px;
  }
  .content {
      margin-top: 20px;
  }

  .queue .div-right, .not-appeared .div-right {
      margin-top: 20px;
      margin-left: 10px;
  }

  .queue .queue-user-div, .not-appeared .not-appeared-user-div {
      height: 145px;
      /* text-align: center; */
  }

  .login-footer, .heading {
      display: none !important;
  }

  .menu-links {
      display: none;
  }

  .mobile-links {
      display: block !important;
  }
}

.mobile-links {
  display: none;
}

/* Layout css end */

/* Footer css begin*/

.login-footer {
  position: fixed;
  bottom: 0;
}

.login-header, .login-footer {
  width: 100%;
  height: 52px;
  background-color: #7fc4fd;
}

/* Footer css end*/

/* Common css begin*/

