.d-common-select {
  box-sizing: border-box;
  width: 100%;
  height: 40px;
  font-size: 14px;
  /* box-shadow: 0 2px 2px 0 rgba(0,0,0,.048); */
  /* border: none; */
  color: #78849E;
  border-radius: 5px;
  border: 0.5px solid gray;
